import { JetView } from "webix-jet";

export default class ChartView extends JetView {
	config() {
		const page = {
			type: "wide",
			margin: 5,
			cols: [{}],
		};

		return page;
	}

	init() {
		this.State = this.getParam("state", true);
		this.Charts = this.app.getService("charts");
		this.Local = this.app.getService("local");
	}
	ready() {
		this.Host = this.getRoot();

		this.on(this.State.$changes, "module", mod => this.Show(mod));
	}

	Show(mod) {
		const host = this.Host;

		if (mod && mod.type === "chart" && this.CheckColumns(mod.columns)) {
			const config = this.GetChartConfig(mod);

			if (!config.view) return;

			this.GetChartData(mod).then(([data, options]) => {
				if (mod.meta.chart.seriesFrom == "rows")
					data = this.ConvertDataBySeries(data);
				config.data = this.FormatLabels(
					data.slice(0, this.Charts.numLimit),
					options
				);
				webix.ui([config], host);
				this.Chart = host.getChildViews()[0];
			});
		} else {
			webix.ui([{ template: " " }], host);
			this.Chart = null;
		}
	}

	FormatLabels(data, options) {
		const labelColumn = this.State.module.meta.chart.labelColumn;

		const labelOptions =
			labelColumn && options.length
				? options.find(op => op.field == labelColumn)
				: null;

		if (labelOptions)
			data.forEach(item => {
				const option = labelOptions.data.find(o => o.id == item[labelColumn]);
				item[labelColumn] = option ? option.value : item[labelColumn];
			});
		else {
			const field = (
				this.State.module.fields || this.State.module.columns
			).find(field => field.id == labelColumn);
			if (field && field.type == "date") {
				data.forEach(item => {
					item[labelColumn] = webix.i18n.dateFormatStr(
						new Date(item[labelColumn])
					);
				});
			}
		}
		return data;
	}

	CheckColumns(columns) {
		const result = columns.length && columns.indexOf(webix.undefined) == -1;
		return result;
	}

	GetChartConfig(mod) {
		const { columns, meta } = mod;
		if (!columns || !meta.chart) return {};

		const {
			chartType,
			labelColumn,
			legendPosition,
			series,
			axises,
		} = meta.chart;

		if (!series) return {};
		const seriesConfig = [];

		series.forEach(s => {
			if (s.show !== false) {
				const type = s.chartType || chartType;
				seriesConfig.push({
					...this.ApplyTypeConfig(s, type),
					value: item => item[s.id],
				});
			}
		});

		let config = {
			view: "chart",
			type: chartType,
			localId: "chart",
			series: seriesConfig,
			padding: {
				left: 80,
				right: 40,
				top: 50,
				bottom: 80,
			},
			on: {
				onAfterRender: () => this.CorrectLabels(),
			},
			legend: this.GetLegendConfig(series, chartType, legendPosition),
		};
		if (axises) {
			config.xAxis = {
				template: item => {
					return (
						"<div class='webix_rpt_chart_label'>" +
						(item[labelColumn] || "") +
						"</div>"
					);
				},
				...axises.x,
			};
			config.yAxis = {
				...axises.y,
			};
			if (axises.y.logarithmic) config.scale = "logarithmic";
			if (axises.x.verticalLabels)
				config.css = "webix_rpt_vertical_chart_labels";
		}
		if (config.legendPosition == "right")
			config.padding.right = config.legend.width + 10;
		else {
			config.padding.bottom = 90;
		}

		return config;
	}

	GetLegendConfig(series, type, legendPosition) {
		if (legendPosition == "none") return null;
		let values = [];
		let text = [];
		const itemTypes = this.Charts.itemSupport;
		series.forEach(s => {
			if (s.show !== false) {
				values.push({
					text: s.meta.name,
					color: s.meta.color,
					markerType:
						itemTypes.indexOf(series.chartType || type) != -1
							? "item"
							: "square",
				});
				text.push(s.name);
			}
		});
		let config = {
			values,
			margin: 8,
		};

		if (legendPosition == "right") {
			config.width = webix.html.getTextSize(text).width + 30;
			config.valign = "middle";
			config.align = "right";
		} else {
			config.layout = "x";
			config.valign = "bottom";
			config.align = "center";
		}
		return config;
	}

	GetChartData(mod) {
		return Promise.all([
			this.Local.getData(
				this.Local.getDataConfig(mod),
				this.State.mode == "edit"
			),
			this.Local.getOptions(mod.columns),
		]);
	}

	ApplyTypeConfig(series, type) {
		const meta = series.meta;
		if (this.Charts.itemSupport.indexOf(type) != -1) {
			return {
				fill: meta.area ? meta.color : null,
				alpha: meta.area ? 0.2 : 1,
				item: {
					type: meta.markerType,
					radius: meta.markerType == "no" ? 0 : 3,
					borderColor: meta.color,
					color: meta.fillMarker ? meta.color : "#ffffff",
				},
				line: {
					color: meta.color,
					width: meta.area ? 1 : 2,
				},
				disableItems: meta.area ? true : false,
			};
		} else if (type == "area" || type == "splineArea")
			return {
				border: true,
				alpha: 0.3,
				color: meta.color,
			};
		else
			return {
				color: meta.color,
			};
	}

	ConvertDataBySeries(data) {
		const {
			labelColumn,
			baseColumn,
			dataColumn,
		} = this.State.module.meta.chart;
		if (!(labelColumn && baseColumn && dataColumn)) return [];
		let arr = [];
		data.forEach(row => {
			let group = arr.find(aRow => aRow[labelColumn] == row[labelColumn]);
			if (!group) {
				group = {};
				group[labelColumn] = row[labelColumn];
			}
			if (!group[row[baseColumn]]) group[row[baseColumn]] = row[dataColumn];
			arr.push(group);
		});

		return arr;
	}

	CorrectLabels() {
		const chart = this.$$("chart");
		if (chart && !chart.config.offset) {
			const res = chart.$view.querySelector(".webix_axis_item_x");
			if (res) res.className += " webix_rpt_chart_label0";
		}
	}
}
