import { JetView } from "webix-jet";

export default class ExportView extends JetView {
	config() {
		return {
			view: "popup",
			padding: 0,
			point: false,
			body: {
				view: "list",
				borderless: true,
				css: "webix_rpt_popup_menu",
				template: obj => this.GetTemplate(obj),
				width: 160,
				autoheight: true,
				data: this.GetPopupOptions(),
				click: (id, e) => this.PopupAction(id, e),
			},
		};
	}

	GetPopupOptions() {
		const _ = this.app.getService("locale")._;
		return [
			{ value: _("To Excel"), id: "excel" },
			{ value: _("To CSV"), id: "csv" },
		];
	}
	PopupAction(id) {
		this.app.callEvent("exportModule", [id]);
		this.getRoot().hide();
	}
	GetTemplate(obj) {
		return `<span class='webix_icon ${obj.icon}'></span>${obj.value}`;
	}
	Show(node) {
		if (this.getRoot().isVisible()) return;
		this.webix.delay(() => this.getRoot().show(node));
	}
}
