import { JetView } from "webix-jet";
import "../../helpers/groupselect";

export default class OtherSettingsView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const groupByMode = {
			cols: [
				{
					view: "switch",
					localId: "groupBySwitch",
					css: "webix_rpt_title",
					width: 65,
					on: {
						onChange: v => this.ToggleGroupBy(v),
					},
				},
				{
					view: "label",
					label: _("Group"),
				},
			],
		};

		const sortByMode = {
			cols: [
				{
					view: "switch",
					localId: "sortBySwitch",
					css: "webix_rpt_title",
					width: 65,
					on: {
						onChange: v => this.ToggleSortBy(v),
					},
				},
				{
					view: "label",
					label: _("Sort"),
				},
			],
		};

		return {
			type: "form",
			rows: [
				groupByMode,
				{ $subview: "_hidden", name: "group" },
				sortByMode,
				{ $subview: "_hidden", name: "sort" },
				{ gravity: 0.000001 },
			],
		};
	}

	init() {
		this.State = this.getParam("state", true);
		this.$$("groupBySwitch").setValue(!!this.State.group);
		this.$$("sortBySwitch").setValue(!!this.State.sort);
	}

	ToggleGroupBy(v) {
		if (v) {
			this.show("./editor.groups", { target: "group" });
		} else {
			this.State.group = null;
			this.show("_hidden", { target: "group" });
		}
	}

	ToggleSortBy(v) {
		if (v) {
			this.show("./editor.sorts", { target: "sort" });
		} else {
			this.State.sort = null;
			this.show("_hidden", { target: "sort" });
		}
	}
}
