export default class Charts {
	constructor(_app) {
		this._app = _app;
		this.type = "line";
		this.type = "line";
		this.colors = [
			"#FF9700",
			"#4CB050",
			"#00BCD4",
			"#3F51B5",
			"#9C28B1",
			"#F34336",
			"#FFEA3B",
			"#009788",
			"#2196F3",
			"#673BB7",
			"#EA1E63",
		];
		this.itemSupport = ["line", "spline", "radar"];
		this.noAxisSupport = ["pie", "donut"];
		this.lineTypes = ["line", "spline"];
		this.multiTypeSupport = [
			"bar",
			"stackedBar",
			"area",
			"stackedArea",
			"splineArea",
		];
		this.numLimit = 30;
	}
	/*
	Get chart type data.
	Create an array of objects {id, value}
	and return this array.
	*/
	getTypes() {
		const _ = this._app.getService("locale")._;
		if (!this.types)
			this.types = [
				{ id: "line", value: _("Line"), icon: "rpi-chart-line" },
				{
					id: "spline",
					value: _("Spline"),
					icon: "rpi-chart-bell-curve-cumulative",
				},
				{
					id: "area",
					value: _("Area"),
					icon: "rpi-chart-areaspline",
				},
				{
					id: "stackedArea",
					value: _("Stacked area"),
					icon: "rpi-chart-line-stacked",
				},
				{
					id: "splineArea",
					value: _("Spline area"),
					icon: "rpi-chart-areaspline-variant",
				},

				{ id: "bar", value: _("Bar"), icon: "rpi-chart-bar" },
				{
					id: "stackedBar",
					value: _("Stacked bar"),
					icon: "rpi-chart-bar-stacked",
				},
				{
					id: "radar",
					value: _("Radar"),
					icon: "rpi-vector-triangle",
				},
				/*{
					id: "pie",
					value: _("chart-pie"),
					icon: "rpi-chart-pie",
					series: false,
				},
				{
					id: "donut",
					value: _("chart-donut"),
					icon: "rpi-chart-donut",
					series: false,
				},*/
			];
		return this.types;
	}

	getItemTypes() {
		const _ = this._app.getService("locale")._;
		if (!this.itemTypes)
			this.itemTypes = [
				{
					id: "r",
					value: _("circle"),

					icon: "rpi-circle-outline",
				},
				{
					id: "s",
					value: _("square"),

					icon: "rpi-square-outline",
				},
				{
					id: "t",
				},
				{
					id: "d",
				},
				{
					id: "no",
					value: _("no markers"),
				},
			];
		return this.itemTypes;
	}
}
