export default class Local {
	constructor(_app) {
		this._app = _app;

		this._fieldData = new Map();
		this._optionsData = new Map();
		this._data = new Map();
	}

	ready(obj) {
		return this.getModels().then(() => obj);
	}

	getModules() {
		if (!this._modules) {
			this._modules = new webix.DataCollection({
				scheme: {
					$change: obj => {
						obj.value = obj.name;
						obj.parsed = JSON.parse(obj.text);
						if (typeof obj.updated === "string")
							obj.updated = new Date(obj.updated);
					},
					$sort: {
						by: "name",
						dir: "asc",
						as: "string",
					},
				},
			});
			this._modules.parse(this._app.getService("backend").getModules());
		}

		return this._modules;
	}

	getModule(id) {
		const mods = this.getModules();
		return mods.waitData.then(() => this._modules.getItem(id));
	}

	deleteModule(id) {
		return this._app
			.getService("backend")
			.deleteModule(id)
			.then(() => {
				this._modules.remove(id);
			});
	}

	copyModule(id) {
		const mod = this._modules.getItem(id);
		return this.saveModule(0, { name: "Copy of " + mod.name, text: mod.text });
	}

	saveModule(id, data) {
		return this._app
			.getService("backend")
			.saveModule(id, data)
			.then(res => {
				if (id) {
					this._modules.updateItem(id, data);
				} else {
					this._modules.add({ id: res.id, ...data });
				}

				return res.id;
			});
	}

	getModels(now) {
		if (now) return this._models || {};

		if (!this._models_async) {
			this._models_async = this._app
				.getService("backend")
				.getModels()
				.then(models => {
					const refs = {};
					Object.keys(models).forEach(m => {
						const obj = models[m];
						obj.data.forEach(f => {
							if (f.type == "reference") {
								if (!refs[m]) refs[m] = [];
								if (!refs[f.ref]) refs[f.ref] = [];
								const id = `${m}/${f.id}//${f.ref}`;
								refs[m].push({ sid: m, tid: f.ref, sf: f.id, id });
								refs[f.ref].push({ sid: f.ref, tid: m, tf: f.id, id });
							}
						});
					});

					this._refs = refs;
					this._models = models;
					return models;
				});
		}

		return this._models_async;
	}

	getRefs() {
		return this._refs;
	}

	getModel(id) {
		return this._models[id];
	}

	getFieldData(field, type) {
		const id = field;
		let list = this._fieldData.get(id);
		if (list) return list;

		list = this._app
			.getService("backend")
			.getFieldData(field)
			.then(list => {
				if (type === "date") list = list.map(a => new Date(a));
				return list;
			});
		this._fieldData.set(id, list);
		return list;
	}

	getFields(data, joins, major) {
		const m = this._models;
		const fields = [];

		m[data].data.forEach(a => fields.push(this.newField(m[data], a)));
		joins.forEach(j =>
			m[j.tid].data.forEach(a => {
				if (!major || a.show) fields.push(this.newField(m[j.tid], a));
			})
		);

		return fields;
	}

	setDataSource(id) {
		const columns = this.getFields(id, []);

		return {
			query: "",
			sort: null,
			fields: columns,
			columns: [...columns],
			oldColumns: null,
			group: null,
			joins: [],
		};
	}

	newField(model, a) {
		const obj = {
			id: model.id + "." + a.id,
			name: a.name,
			type: a.type,
			ref: a.ref,

			mid: model.id,
			model: model.name,

			meta: {},
		};

		if (a.key) obj.key = true;

		return obj;
	}

	newModule(type) {
		const _ = this._app.getService("locale")._;
		const name = _("New report");
		const desc = _("Created on") + " " + webix.i18n.dateFormatStr(new Date());

		return {
			id: 0,
			name,
			desc,
			data: Object.keys(this._models)[0],
			joins: [],
			query: "",
			type: type || "table",
			sort: null,
			columns: [],
			fields: [],
			publicFields: null,
			group: null,

			meta: {},
		};
	}

	addJoin(id, config) {
		const model = this._models[id];
		const { fields, columns, group } = config;
		model.data.forEach(a => {
			fields.push(this.newField(model, a));
			if (a.show && !group) columns.push(this.newField(model, a));
		});
		return { fields, columns };
	}

	getData(config, limit) {
		let id = JSON.stringify(config);
		let list = this._data.get(id);
		if (list) return list;

		if (limit) {
			config.limit = 30;
			id = JSON.stringify(config);
			list = this._data.get(id);
			if (list) return list;
		}

		list = this._app.getService("backend").getData(config);
		this._data.set(id, list);
		return list;
	}

	getDataConfig(module) {
		return {
			data: module.data,
			query: module.query,
			// sort is necessary to enforce data caching for request with different column order
			columns: module.columns.map(a => a.id).sort(),
			joins: module.joins,
			sort: module.sort,
			group:
				module.group && module.group.by
					? module.group.by.map(a => (a.mod ? a.mod + "." : "") + a.id)
					: "",
		};
	}

	getOptions(columns) {
		const options = columns
			.filter(a => a.type === "picklist" || a.type === "reference")
			.map(a => this.getOptionsList(a.id));

		return Promise.all(options);
	}

	getOptionsList(field) {
		let list = this._optionsData.get(field);
		if (list) return list.then(data => ({ field, data }));

		list = this._app.getService("backend").getOptions(field);
		this._optionsData.set(field, list);
		return list.then(data => ({ field, data }));
	}

	getLinkedModels(base, onlyNew) {
		const out = [];
		const refs = this.getRefs();

		for (var i = 0; i < base.length; i++) {
			const sid = base[i];
			if (!refs[sid]) continue;

			refs[sid].forEach(ref => {
				if (onlyNew && base.indexOf(ref.tid) != -1) return;
				out.push({ ...ref });
			});
		}

		return out;
	}

	cleanLinkedModels(id, config) {
		let { data, joins, fields, columns, query, group, publicFields } = config;
		const was = { [data]: 1 };
		let next,
			origin = joins,
			links = [];

		joins = next = joins.filter(a => a.id != id);
		do {
			joins = next;
			next = [];

			for (let i = 0; i < joins.length; i++) {
				if (was[joins[i].sid]) was[joins[i].tid] = 1;
				else next.push(joins[i]);
			}
		} while (next.length && next.length !== joins.length);

		let queryObj = query ? collectTables(JSON.parse(query)) : [];
		for (let i = 0; i < origin.length; i++) {
			const j = origin[i];
			if (was[j.tid]) {
				links.push(j);
			} else {
				// can't remove table if it already used in filters
				if (queryObj.indexOf(j.tid) !== -1) return { block: { filter: 1 } };
				if (group) {
					const check = "." + j.tid + ".";
					// [FIXME] add model prop for group custom fields
					if (publicFields.find(a => a.id.indexOf(check) != -1))
						return { block: { group: 1 } };
				}
				columns = columns.filter(a => a.mid != j.tid);
				fields = fields.filter(a => a.mid != j.tid);
			}
		}

		return {
			joins: links,
			columns,
			fields,
		};
	}

	getQueries() {
		if (this._queries) return this._queries;

		return (this._queries = new webix.DataCollection({
			scheme: {
				$change: obj => {
					obj.value = obj.name;
					obj.models = collectTables(JSON.parse(obj.text));
				},
			},
			data: this._app.getService("backend").getQueries(),
		}));
	}

	saveQuery(id, data) {
		return this._app
			.getService("backend")
			.saveQuery(id, data)
			.then(res => {
				if (id) {
					this._queries.updateItem(id, data);
				} else {
					this._queries.add({ id: res.id, ...data });
				}

				return res.id;
			});
	}

	deleteQuery(id) {
		return this._app
			.getService("backend")
			.deleteQuery(id)
			.then(() => {
				this._queries.remove(id);
			});
	}
}

function collectTables(q, mods) {
	mods = mods || [];
	if (q.rules)
		q.rules.forEach(r => {
			if (r.rules) collectTables(r, mods);
			else {
				const parts = r.field.split(".");
				mods.push(parts[0]);
			}
		});
	return mods;
}
