export default class Helpers {
	constructor() {
		this.replacements = [
			{ from: /(^ +)/g, to: "" },
			{ from: /(^\w+)/g, to: "^($&)" },
			{ from: /^(\?)/g, to: "^\\S{1}" },
			{ from: /(\?)/g, to: "\\S{1}" },
			{ from: /(\*)/g, to: "\\S*" },
			{ from: /( +)$/g, to: "$" },
		];
	}

	wildToRegexp(wild) {
		for (let i = 0; i < this.replacements.length; ++i) {
			wild = wild.replace(this.replacements[i].from, this.replacements[i].to);
		}
		return wild;
	}
	matchPerColumn(id, match) {
		return a => match(a[id]);
	}

	findOptions(collection, regexp) {
		const res = {};
		collection
			.find(obj => regexp.test(obj.value))
			.forEach(a => (res[a.id] = 1));
		return res;
	}

	hasNonDigits(input) {
		// exceptions are: ?, *, /, ., space
		return /\D/gi.test(input.replace(/[?*/. ]/g, ""));
	}
}
