import { JetView } from "webix-jet";
import { withContext, createState, debounce } from "jet-restate";

export default class TopView extends JetView {
	config() {
		const _ = this.app.getService("locale")._;
		const tabbar = {
			optionWidth: 102,
			tabMinWidth: 102,
			view: "tabbar",
			localId: "tabbar",
			css: "webix_rpt_editor_tabbar",
			type: "bottom",
			borderless: true,
			options: [
				{ id: "common", value: _("Common") },
				{ id: "data", value: _("Query") },
				{ id: "other", value: _("Data") },
				{ id: "structure", value: _("View") },
			],
			on: {
				onChange: v => this.ShowView(v),
			},
		};

		const toolbar = {
			view: "toolbar",
			padding: { left: 13, top: 0, bottom: 0 },
			//padding: { left: 13, top: webix.skin.$active.layoutPadding.toolbar, bottom: webix.skin.$active.layoutPadding.toolbar },
			height: webix.skin.$active.barHeight + 6,
			scroll: false,
			cols: [
				tabbar,
				{
					view: "icon",
					width: 40,
					localId: "closeBtn",
					icon: "rpi-close",
					click: () => this.Hide(),
				},
			],
		};

		const ui = {
			width: 490,
			hidden: false,
			localId: "settings",
			rows: [toolbar, { $subview: true }],
		};

		this.Local = this.app.getService("local");
		return this.Local.ready(ui);
	}

	init() {
		this.AppState = this.app.getParam("state");

		const initial = this.Local.newModule();
		initial.id = -1; // mark as temporary

		const state = (this.State = createState(initial));
		this.setParam("state", this.State);

		this.on(this.AppState.$changes, "moduleId", v => this.ChangeModule(v));
		this.on(this.State.$changes, "data", (id, o, k, ctx) => {
			if (id && (!ctx || !ctx.load)) this.ChangeModel(id);
		});

		this.on(this.AppState.$changes, "toolbar", v => this.ShowHideButton(v));

		this.on(
			this.State.$changes,
			"*",
			debounce(() => {
				if (this.State.id === -1) return;

				this.TrackChanges();
				this.AppState.module = Object.assign({}, this.State);
			})
		);

		this.on(state.$changes, "group", (v, old, _key, ctx) => {
			if (!v && old) {
				// revert back
				state.$batch({
					publicFields: null,
					columns:
						state.oldColumns ||
						this.Local.getFields(state.data, state.joins, true),
				});
			} else if (v) {
				let prev = [];
				if (!ctx || !ctx.load) {
					if (!old) state.oldColumns = state.columns;
					else {
						if (JSON.stringify(v) == JSON.stringify(old)) return;
						prev = [...this.State.columns];
					}
				} else {
					prev = [...this.State.columns];
				}

				// group mode changed, add new columns
				let columns = v.columns.map(a => ({
					id: `${a.op}.${a.id}`,
					name: a.name,
					type: "number",
					meta: {},
					model: "",
				}));
				if (v.by)
					columns = columns.concat(
						v.by.map(a => {
							const base = this.State.fields.find(b => b.id == a.id);
							const id = a.mod ? `${a.mod}.${a.id}` : a.id;
							const obj = { ...base, id };
							if (a.mod) obj.type = "text";
							return obj;
						})
					);

				// apply modifications
				const was = {};
				(state.publicFields || state.columns).forEach(a => (was[a.id] = 1));
				const now = {};
				// add new
				columns.forEach(a => {
					now[a.id] = 1;
					if (!was[a.id]) prev.push(a);
				});
				//remove old
				prev = prev.filter(a => now[a.id]);

				if (state.meta.freeze && state.meta.freeze >= prev.length)
					delete state.meta.freeze;

				state.$batch({ publicFields: columns, columns: prev });
			}
		});

		this.on(this.app, "saveModule", () => {
			const save = this.SaveData();
			const text = JSON.stringify(save);

			this.Local.saveModule(this.AppState.moduleId, {
				name: this.State.name,
				text,
			}).then(id => {
				this.AppState.$batch({
					moduleId: id,
					mode: "view",
					module: Object.assign({}, this.AppState.module),
					saved: true,
				});
			});
		});

		this.on(this.app, "resetModule", () => this.Reset());

		this.on(this.app, "onColumnResize", (id, width) => {
			var next = [...this.State.columns];
			next.find(a => a.id == id).width = width;
			this.State.columns = next;
		});

		this.on(this.AppState.$changes, "readonly", mod => {
			if (mod) this.Reset();
		});

		this.show("./editor.common");
	}

	ShowView(v) {
		this.$$("tabbar").setValue(v);
		this.show("editor." + v);
	}

	Hide() {
		//this.AppState.mode = "view";
		this.Reset();
	}

	Reset() {
		const id = this.AppState.moduleId;
		if (!id) {
			this.AppState.$batch({ mode: "list", module: null, saved: true });
		} else
			this.Local.getModule(id).then(mod => {
				this.AppState.$batch({
					mode: "view",
					module: Object.assign({}, JSON.parse(mod.text)),
					saved: true,
				});
			});
	}

	ChangeModel(id) {
		if (id) this.State.$batch(this.Local.setDataSource(id));
	}

	ChangeModule(id) {
		this.InitStateValue = "";
		if (id) {
			this.Local.getModule(id).then(item => {
				const obj = JSON.parse(item.text);

				this.InitStateValue = item.text;
				this.InitName = obj.name = item.name;

				obj.id = item.id;
				obj.saved = true;
				obj.fields = this.Local.getFields(obj.data, obj.joins);

				withContext({ load: true }, () => this.State.$batch(obj));
				this.AppState.saved = true;
			});
		} else {
			this.InitStateValue = null; // we need to have an always active Save button
			this.State.$batch(this.Local.newModule());
			this.State.fields = this.Local.getFields(
				this.State.data,
				this.State.joins
			);
			this.AppState.saved = false;
		}
	}
	TrackChanges() {
		this.AppState.saved =
			JSON.stringify(this.SaveData()) == this.InitStateValue &&
			this.State.name == this.InitName;
	}
	SaveData() {
		const {
			desc,
			data,
			joins,
			query,
			columns,
			group,
			meta,
			sort,
			type,
		} = this.State;
		return { desc, data, joins, query, columns, group, meta, sort, type };
	}

	ShowHideButton(v) {
		if (v) this.$$("closeBtn").show();
		else this.$$("closeBtn").hide();
	}
}
