import { JetView } from "webix-jet";

export default class ChartView extends JetView {
	config() {
		const page = {
			type: "wide",
			margin: 5,
			cols: [{}],
		};

		return page;
	}

	init() {
		this.State = this.getParam("state", true);
		this.Charts = this.app.getService("charts");
		this.Local = this.app.getService("local");
	}
	ready() {
		this.Host = this.getRoot();

		this.on(this.State.$changes, "module", mod => this.Show(mod));
	}

	Show(mod) {
		const host = this.Host;
		if (mod && mod.meta.value && mod.type === "heatmap") {
			const config = this.GetChartConfig(mod);

			if (!config.view) return;

			this.GetChartData(mod).then(([data]) => {
				config.data = data;
				if (data.length && mod.meta.color)
					config.type.cssClass = this.CssFactory(data, mod.meta.color);

				webix.ui([config], host);
				this.Chart = host.getChildViews()[0];
			});
		} else {
			webix.ui([{ template: " " }], host);
			this.Chart = null;
		}
	}

	GetChartConfig(mod) {
		const { meta, columns } = mod;

		const obj = {
			view: "treemap",
			css: "webix_rpt",
			value: obj => obj[meta.value],
			tooltip: this.TooltipFactory(meta, columns),
			type: {},
		};

		if (meta.label) obj.type.template = obj => obj[meta.label];

		return obj;
	}

	GetChartData(mod) {
		return Promise.all([
			this.Local.getData(
				this.Local.getDataConfig(mod),
				this.State.mode == "edit"
			),
			this.Local.getOptions(mod.columns),
		]);
	}

	CssFactory(data, prop) {
		var min = Infinity;
		var max = -Infinity;

		for (var i = 0; i < data.length; i++) {
			const v = data[i][prop];
			if (v > max) max = v * 1;
			if (v < min) min = v * 1;
		}

		const step = (max - min) / 4;
		const bases = [max - step, max - step * 2, max - step * 3, min];

		return function(obj) {
			const v = obj[prop];
			for (var i = 0; i < bases.length; i++) if (v >= bases[i]) return "l" + i;
		};
	}

	TooltipFactory(obj, columns) {
		const props = [obj.value, columns.find(a => a.id === obj.value).name];
		if (obj.color && obj.color != obj.value)
			props.push(obj.color, columns.find(a => a.id === obj.color).name);

		return function(obj) {
			let out = "";
			for (let i = 0; i < props.length; i += 2) {
				out += (i ? ", " : "") + props[i + 1] + ": " + obj[props[i]];
			}

			return out;
		};
	}
}
