import { JetView } from "webix-jet";

export default class TopView extends JetView {
	config() {
		const page = {
			type: "wide",
			margin: 5,

			cols: [
				{
					$subview: true,
					name: "left",
				},
				{ $subview: true, name: "right", css: "webix_rpt_right" },
			],
		};

		return {
			type: "wide",
			margin: 5,
			css: "webix_reports",
			rows: [{ $subview: true, name: "top" }, page],
		};
	}

	init() {
		this.State = this.app.getParam("state");

		const _ = this.app.getService("locale")._;

		this.on(this.State.$changes, "toolbar", v => {
			this.show(v ? "toolbar" : "_hidden", { target: "top" });
		});

		this.on(this.State.$changes, "mode", v => {
			if (v === "edit") this.show("./editor", { target: "left" });
			else if (v === "list") this.show("./modules", { target: "left" });
			else this.show("_hidden", { target: "left" });
		});

		this.on(this.State.$changes, "moduleId", id => {
			if (id)
				this.app
					.getService("local")
					.getModule(id)
					.then(mod => {
						this.State.module = JSON.parse(mod.text);
					});
			else this.State.module = null;
		});

		this.on(this.State.$changes, "module", mod => {
			let type = (mod ? mod.type : "empty") || "table";
			this.show("./" + type, { target: "right" });
		});

		this.on(this.app, "loadModule", (id, mode) => this.LoadModule(id, mode));

		this.on(this.app, "onMenuAction", (id, action) => {
			if (!id && this.State.moduleId) id = this.State.moduleId;
			if (!id) return;

			if (action === "edit") {
				this.LoadModule(id, "edit");
			} else if (action === "delete") {
				webix
					.confirm({
						title: _("Delete report"),
						ok: _("Delete"),
						cancel: _("Cancel"),
						text: _("Are you sure to delete this report?"),
					})
					.then(() => {
						this.app
							.getService("local")
							.deleteModule(id)
							.then(() => {
								if (id == this.State.moduleId)
									this.State.$batch({ moduleId: null, mode: "list" });
							});
					});
			} else if (action === "copy") {
				this.app
					.getService("local")
					.copyModule(id)
					.then(id => {
						this.State.$batch({ moduleId: id, mode: "edit" });
					});
			}
		});

		this.on(this.app, "showModules", v => {
			if (v) {
				this.State.mode = "list";
			} else if (this.State.mode == "list") {
				this.State.mode = "view";
			}
		});
	}

	LoadModule(id, mode) {
		this.State.moduleId = id;
		if (mode) this.State.mode = mode;
	}
}
