export default class Backend {
	constructor(app) {
		this.app = app;
		this._url = app.config.url;
	}

	//
	// QUERIES
	//

	saveQuery(id, data) {
		if (!id) {
			return webix
				.ajax()
				.post(`${this._url}api/queries`, data)
				.then(a => a.json());
		} else {
			return webix
				.ajax()
				.put(`${this._url}api/queries/${id}`, data)
				.then(a => a.json());
		}
	}

	deleteQuery(id) {
		return webix
			.ajax()
			.del(`${this._url}api/queries/${id}`)
			.then(a => a.json());
	}

	getQueries() {
		return webix.ajax(`${this._url}api/queries`).then(a => a.json());
	}

	//
	// MODULES
	//

	getModules() {
		return webix.ajax(`${this._url}api/modules`).then(a => a.json());
	}
	saveModule(id, data) {
		if (id) {
			return webix
				.ajax()
				.put(`${this._url}api/modules/${id}`, data)
				.then(a => a.json());
		} else {
			return webix
				.ajax()
				.post(`${this._url}api/modules`, data)
				.then(a => a.json());
		}
	}
	deleteModule(id) {
		return webix
			.ajax()
			.del(`${this._url}api/modules/${id}`)
			.then(a => a.json());
	}

	//
	// Meta info
	//

	getModels() {
		return webix.ajax(`${this._url}api/objects`).then(a => a.json());
	}
	getData(config) {
		const { query, columns, joins, limit, group, sort } = config;
		return webix
			.ajax()
			.post(`${this._url}api/objects/${config.data}/data`, {
				query,
				columns,
				joins,
				limit,
				sort,
				group,
			})
			.then(a => a.json());
	}
	getOptions(field) {
		return webix
			.ajax(`${this._url}api/fields/${field}/options`)
			.then(a => a.json());
	}
	getFieldData(field) {
		return webix
			.ajax(`${this._url}api/fields/${field}/suggest`)
			.then(a => a.json());
	}
}
