import { JetView } from "webix-jet";
import key from "jet-hotkey";

export default class TopBarView extends JetView {
	config() {
		const app = this.app;

		const _ = this.app.getService("locale")._;

		const bar = {
			view: "toolbar",
			localId: "bar",
			padding: { left: 10, right: 10, top: 6, bottom: 6 },
			margin: 10,
			visibleBatch: "default",
			height: webix.skin.$active.barHeight + 6,
			elements: [
				{
					localId: "defaultButtons",
					margin: 10,
					width: 300,
					cols: [
						{
							view: "toggle",
							type: "icon",
							label: _("All reports"),
							localId: "list",
							width: 150,
							offIcon: "rpi-menu",
							onIcon: "rpi-menu-open",
							on: {
								onChange: function() {
									app.callEvent("showModules", [this.getValue()]);
								},
							},
						},

						{
							view: "button",
							type: "icon",
							localId: "newBtn",
							//css: "webix_primary",
							icon: "rpi-plus",
							label: _("New"),
							click: () => {
								this.$$("bar").showBatch("edit");
								app.callEvent("loadModule", [null, "edit"]);
							},
						},
					],
				},
				{ width: 300, batch: "edit" },
				{ view: "label", localId: "name", align: "center", label: "" },
				{ width: 330, batch: "default" },

				{
					batch: "report",
					width: 290,
					cols: [
						{},
						{
							view: "button",
							type: "icon",
							align: "right",
							css: "webix_primary",
							icon: "wxi-pencil",
							label: _("Edit"),
							width: 140,
							click: () => app.callEvent("onMenuAction", [null, "edit"]),
						},
					],
				},
				{
					width: 290,
					batch: "edit",
					margin: 10,
					cols: [
						{
							view: "button",
							type: "icon",
							icon: "rpi-close",
							label: _("Cancel"),
							localId: "reset",
							//disabled: true,
							click: () => app.callEvent("resetModule", []),
						},
						{
							view: "button",
							css: "webix_primary",
							type: "icon",
							icon: "wxi-check",
							localId: "save",
							width: 140,
							label: _("Save"),
							disabled: true,
							click: () => app.callEvent("saveModule", []),
						},
					],
				},

				{ popup: true, $subview: true, name: "popup" },
			],
		};

		return bar;
	}

	init() {
		this.State = this.app.getParam("state", true);

		this.on(this.State.$changes, "module", mod => {
			if (mod) {
				if (typeof mod.name != "undefined") this.$$("name").setValue(mod.name);
				else if (this.State.moduleId)
					this.app
						.getService("local")
						.getModule(this.State.moduleId)
						.then(module => {
							this.$$("name").setValue(module.name);
						});
			} else {
				this.$$("name").setValue("");
			}
		});

		this.on(this.State.$changes, "saved", v => {
			const name = this.$$("name").$view;
			if (v) {
				this.$$("save").disable();
				//this.$$("reset").disable();
				name.classList.remove("webix_rpt_not_saved");
			} else {
				this.$$("save").enable();
				//this.$$("reset").enable();
				name.classList.add("webix_rpt_not_saved");
			}
		});

		this.on(this.State.$changes, "mode", () => this.SetVisibleButtons());
		this.on(this.State.$changes, "moduleId", () => this.SetVisibleButtons());
		this.on(this.State.$changes, "readonly", () => this.SetVisibleButtons());

		this.on(key(), "ctrl+e", () => {
			this.app.callEvent("onMenuAction", [null, "edit"]);
			return false;
		});

		this.on(key(), "ctrl+s", () => {
			this.app.callEvent("saveModule", []);
			return false;
		});

		this.on(key(), "ctrl+l", () => {
			this.app.callEvent("showModules", [true]);
			return false;
		});
	}

	SetVisibleButtons() {
		const mode = this.State.mode;
		if (mode == "list") {
			this.$$("list").setValue(true);
		} else {
			this.$$("list").setValue(false);
		}
		if (mode == "edit") {
			this.$$("defaultButtons").hide();
			this.$$("bar").showBatch("edit");
		} else {
			this.$$("defaultButtons").show();
			if (this.State.moduleId && !this.State.readonly) {
				this.$$("bar").showBatch("report");
			} else {
				this.$$("bar").showBatch("default");
			}
		}
		const newBtn = this.$$("newBtn");
		if (newBtn) {
			if (this.State.readonly) {
				if (newBtn.isVisible()) newBtn.hide();
			} else if (!newBtn.isVisible()) {
				newBtn.show();
			}
		}
	}

	TitleTemplate(data) {
		return `<div class="rep_title">${data.name}</div>`;
	}
}
